// 1. グローバル変数を定義する
let GlobalData = new Vue({
  data: {
    $useremail: "", // グローバル変数
    $channel: "",
    $username: "",
    $password: ""
  }
});

Vue.mixin({
  methods: {
    // 2-1. 各コンポーネント内の<script>タグ内でグローバル変数を参照するための関数
    $_getuseremail(){
      return GlobalData.$data.$useremail
    },
    $_setuseremail(newUserEmail){
      GlobalData.$data.$useremail = newUserEmail
    },
    $_getchannel(){
      return GlobalData.$data.$channel
    },
    $_setchannel(newCha){
      GlobalData.$data.$channel = newCha
    },
    $_getusername(){
      return GlobalData.$data.$username
    },
    $_setusername(newName){
      console.log(newName)
      GlobalData.$data.$username = newName
    },
    $_getpassword(){
      return GlobalData.$data.$password
    },
    $_setpassword(newPass){
      GlobalData.$data.$password = newPass
    }
  }
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
