<template>
  <div id="app">
    <div class="sign">
      <div class="signin">
        <h2>Sign in</h2>
        <input type="text" placeholder="Email Adress" v-model="INusername">
        <input type="password" placeholder="Password" v-model="INpassword">
        <button @click="signIn">login</button>
      </div>
      <div class="signup">
        <h2>Sign up</h2>
        <input type="text" placeholder="Email Adress" v-model="UPusername">
        <input type="password" placeholder="Password" v-model="UPpassword">
        <button @click="signUp">register</button>
      </div>
    </div>
    ページ番号：{{this.userPageNum}}
    <router-view/>
  </div>
</template>

<script>
import firebase from 'firebase'
import axios from "axios"

export default {
  name: 'HelloWorld',
  data:()=>{
    return {
      UPusername: '',
      UPpassword: '',
      INusername: '',
      INpassword: '',
      message: '',
      userPageNum:'',
      logoutText:''
    }
  },
  methods: {
    signUp: function () {
      firebase.auth().createUserWithEmailAndPassword(this.UPusername, this.UPpassword)
      .then(user => {
        alert('Create account: ', user.email)
      })
      .catch(error => {
        alert(error.message)
      })
    },
    signIn: function () {
      firebase.auth().signInWithEmailAndPassword(this.INusername, this.INpassword).then(
        user => {
          this.emailRequest(this.INusername)
          this.$_setuseremail(this.INusername)
          this.setIdRequest(this.INusername)
          this.$router.push('/about').catch(()=>{});
          console.log(user)
        },
        err => {
          alert(err.message)
        }
      )
    },
    emailRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const postEmail = await axios.post("http://localhost:8040/postEmail", params)
      this.userPageNum = postEmail.data
    },
    setIdRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const IDs = await axios.post("http://localhost:8040/setIDs", params)
      console.log(IDs.data)
      //this.$_setchannel(IDs.data.IDs.channels)
      //this.$_setusername(IDs.data.username)
      //this.$_setpassword(IDs.data.password)
    }
  }
}
</script>

